// ## fonts
// Save fonts to /css/fonts, after that write (eot, woff, ttf):
// +font(book, cytiapro-black-webfont)
// =book
// 	font-family: "book", Arial, sans-serif

$blue: #2BC1D6;
$pink: #EE4288;
$pink-light: #ef4f8d;
$gray: #D9DADB;
$gray-light: #F1F2F2;
$gray-dark: #939598;
$black: #231F20;
$border: #cccdd0;
$border-light: #f1f2f3;

@include font(Nevis, Nevis-Bold);
@mixin nevis {
	font-family: "Nevis", Arial, sans-serif; }
@include font(National-Regular, National-Regular);
@mixin regular {
	font-family: "National-Regular", Arial, sans-serif; }
@include font(National-Medium, National-Medium);
@mixin medium {
	font-family: "National-Medium", Arial, sans-serif; }
@include font(National-Bold, National-Bold);
@mixin semibold {
	font-family: "National-Semibold", Arial, sans-serif; }
@include font(National-Bold, National-Bold);
@mixin bold {
	font-family: "National-Bold", Arial, sans-serif; }
@include font(National-Extrabold, National-Extrabold);
@mixin extrabold {
	font-family: "National-Extrabold", Arial, sans-serif; }
@include font(National-Light, National-Light);
@mixin light {
	font-family: "National-Light", Arial, sans-serif; }
@include font(National-LightItalic, National-LightItalic);
@mixin lightitalic {
	font-family: "National-LightItalic", Arial, sans-serif; }
@include font(National-Semibold, National-Semibold);

@mixin fonticon {
	font-family: "iconfont";
	font-style: normal;
	font-weight: normal;
	text-rendering: auto;
	speak: none;
	line-height: 1;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale; }

body {
	min-width: 320px;
	@include light;
	font-size: 15px;
	line-height: 1.3;
	color: $black;
	@include rm(719) {
		font-size: 18px; } }
button {
	background: none; }
a {
	color: $blue;
	&:hover {
		text-decoration: none; } }

.wrapper {
	margin: 0 auto;
	padding-bottom: 50px; }

.center {
	max-width: 1066px;
	margin: 0 auto;
	padding: 0 20px;
	@include rm(719) {
		padding: 0 35px; } }

.layout {
	@include clr;
	@include rm(767) {
		margin-bottom: 60px; } }

.section {
	@include rm(767) {
		float: left;
		width: 52%; } }

.aside {
	@include rm(767) {
		float: right;
		width: 42%; }
	@include rm(1023) {
		width: 39%; } }

.column {
	width: 100%;
	max-width: 500px;
	margin: 0 auto;
	@include rm(767) {
		max-width: 100%;
		margin: 0; }
	&_left {
		@include rm(767) {
			float: left;
			width: 40%; } }
	&_right {
		@include rm(767) {
			width: 50%;
			float: right; } } }

.header {
	min-height: 68px;
	margin-bottom: 30px;
	padding-bottom: 5px;
	border-bottom: 1px solid #D9DADB;
	@include rm(719) {
		margin-bottom: 70px;
		padding-bottom: 8px; }
	&__center {
		@include clr; }
	&__logo {
		float: left;
		margin-top: 25px;
		@include nevis;
		font-size: 27px;
		color: $black;
		letter-spacing: -2px;
		text-decoration: none;
		@include rm(719) {
			margin-top: 21px;
			font-size: 31px; } }
	&_campaign {
		@include r(766) {
			margin-bottom: -68px;
			padding: 0;
			border: none; } }
	&_campaign &__logo {
		@include r(766) {
			display: none; } } }

.main {
	height: 300px;
	padding-top: 150px;
	background: url(../img/bg-main-sm.jpg) no-repeat 50% 50% / cover;
	margin-bottom: 40px;
	text-align: center;
	@include rm(719) {
		height: 470px;
		margin: -70px 0 65px;
		background: url(../img/bg-main.jpg) no-repeat 50% 50% / cover;
		text-align: left; }
	&__logo {
		@include nevis;
		font-size: 45px;
		color: #f3f2f3;
		letter-spacing: -2px;
		text-decoration: none;
		line-height: 1;
		@include rm(719) {
			display: none; }
		&-white {
			color: #fff; } }
	&__title {
		@include lightitalic;
		font-size: 17px;
		color: #fff;
		@include rm(719) {
			width: 400px;
			@include extrabold;
			font-size: 48px;
			line-height: 1; } } }

.nav {
	float: right;
	margin-top: 30px;
	@include rm(767) {
		margin-top: 35px; }
	&__btn {
		height: 22px;
		padding: 0 5px;
		border: 1px solid;
		@include light;
		font-size: 16px;
		text-transform: uppercase;
		letter-spacing: 2px;
		@include rm(719) {
			height: 26px;
			font-size: 21px; }
		@include rm(767) {
			display: none; } }
	&__list {
		display: none;
		float: right;
		@include rm(767) {
			display: block; } }
	&__group {
		float: left;
		margin-left: 60px;
		&:first-child {
			margin: 0; } }
	&__link {
		float: left;
		margin-left: 20px;
		@include regular;
		font-size: 16px;
		color: #000;
		text-decoration: none;
		&:first-child {
			margin: 0; } }
	&_white &__btn {
		@include r(766) {
			color: #FFF; } } }

.content {
	margin-bottom: 30px;
	line-height: 1.4;
	@include rm(719) {
		margin-bottom: 50px;
		line-height: 1.8; }
	h1,
	h2,
	h3,
	h3,
	h4,
	h5,
	h6 {
		@include medium;
		font-weight: 400; }
	h4 {
		font-size: 18px;
		@include rm(719) {
			margin-bottom: 20px;
			font-size: 24px; } }
	h6 {
		font-size: 15px;
		color: $blue;
		@include rm(719) {
			font-size: 18px; } }
	p {
		margin-bottom: 20px; }
	img {
		max-width: 100%; }
	table {
		max-width: 490px;
		margin: 0 auto;
		th,
		td {
			padding: 15px 10px;
			border: 1px solid $border;
			text-align: center; }
		th {
			@include semibold; } } }

.author {
	margin-bottom: 5px;
	@include clr;
	@include rm(719) {
		margin-bottom: 15px;
		padding-bottom: 13px;
		border-bottom: 1px solid $border-light; }
	@include rm(767) {
		margin-bottom: 20px;
		padding-bottom: 0;
		border: none; }
	&__ava {
		float: left;
		width: 46px;
		margin-right: 10px;
		@include rm(719) {
			margin-right: 20px;
			width: 54px; }
		&-el {
			display: block;
			max-width: 100%;
			border-radius: 50%; } }
	&__info {
		display: table;
		@include rm(719) {
			line-height: 1.1; } }
	&__name {
		@include lightitalic;
		font-size: 18px;
		color: $gray-dark;
		@include rm(719) {
			font-size: 24px; } }
	&__title {
		@include extrabold;
		font-size: 22px;
		@include rm(719) {
			font-size: 30px; } } }

.price {
	margin-bottom: 20px;
	font-size: 35px;
	color: $gray-dark;
	text-align: center;
	text-transform: uppercase;
	line-height: 1;
	@include rm(719) {
		margin-bottom: 40px; }
	@include rm(767) {
		margin-bottom: 15px;
		padding: 10px 0 15px;
		border-bottom: 1px solid $border-light;
		@include extrabold;
		text-align: left;
		font-size: 30px;
		color: $black; } }

.slider {
	position: relative;
	margin: 0 -20px 20px;
	@include rm(719) {
		margin: 0 -35px 40px; }
	@include rm(767) {
		margin: 0 0 40px;
		padding-bottom: 40px;
		border-bottom: 1px solid $border-light; }
	&__list {
		padding-bottom: 20px;
		font-size: 0;
		text-align: center;
		@include rm(719) {
			padding-bottom: 30px; }
		.slick-prev {
			left: 10px;
			@include rm(767) {
				left: -25px; } }
		.slick-next {
			right: 10px;
			@include rm(767) {
				right: -25px; } } }
	&__item {
		position: relative; }
	&__item-pic {
		display: inline-block;
		max-width: 100%;
		vertical-align: top; }
	&__label {
		position: absolute;
		top: 10px;
		left: 20px;
		color: $blue;
		@include rm(719) {
			left: 35px; }
		.icon {
			font-size: 94px;
			@include rm(719) {
				font-size: 140px; } } }
	&__previews {
		display: none;
		max-width: 476px;
		margin: 0 auto;
		font-size: 0;
		@include rm(719) {
			display: block; }
		&-item {
			width: 68px;
			padding: 2px 0;
			text-align: center; }
		&-img {
			max-width: 58px;
			max-height: 58px;
			border: 1px solid $border;
			cursor: pointer; }
		.slick-center &-img {
			border-color: $blue;
			box-shadow: 0 0 0 2px $blue; }
		.slick-prev {
			left: -25px; }
		.slick-next {
			right: -25px; } }
	.slick-prev,
	.slick-next {
		position: absolute;
		top: 50%;
		margin-top: -20px;
		padding: 10px;
		font-size: 0;
		color: $gray;
		z-index: 2;
		&:before {
			@include fonticon;
			font-size: 20px; } }
	.slick-prev {
		&:before {
			content: "\EA04"; } }
	.slick-next {
		&:before {
			content: "\EA05"; } }
	.slick-dots {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		text-align: center;
		font-size: 0;
		li {
			display: inline-block;
			padding: 0 3px;
			vertical-align: top;
			&.slick-active {
				button {
					background: $pink; } } }
		button {
			width: 10px;
			height: 10px;
			border-radius: 50%;
			background: $gray;
			font-size: 0; } } }

.product {
	margin-bottom: 30px;
	@include rm(719) {
		margin-bottom: 55px; }
	&__type,
	&__title {
		text-align: center;
		@include rm(767) {
			text-align: left; } }
	&__type {
		font-size: 16px;
		letter-spacing: 3px; }
	&__title {
		margin-bottom: 20px;
		@include extrabold;
		font-size: 28px;
		@include rm(719) {
			margin-bottom: 30px; } }
	&__options {
		margin-bottom: 30px;
		&-item {
			display: none; } }
	&__msg {
		@include lightitalic;
		text-align: center; }
	&__btn {
		width: 100%;
		margin-bottom: 7px; }
	&__content {
		@include rm(767) {
			margin-bottom: 20px;
			padding-bottom: 30px;
			border-bottom: 1px solid $border-light; } } }

.sizes {
	margin-bottom: 35px;
	&__head {
		font-size: 16px; }
	&__wrap {
		display: none;
		padding-top: 25px; }
	&__content {
		margin-bottom: 25px; }
	&__product {
		position: relative;
		margin-bottom: 10px;
		padding-left: 18px;
		&:before {
			content: "";
			position: absolute;
			top: 6px;
			left: 5px;
			width: 6px;
			height: 6px;
			background: $black;
			border-radius: 50%; } }
	&__table {
		width: 100%;
		&-pic {
			display: block;
			max-width: 100%; }
		th {
			height: 44px;
			padding: 0 10px;
			background: $blue;
			border: 1px solid $blue;
			@include extrabold;
			color: #FFF;
			vertical-align: middle; }
		td {
			height: 40px;
			padding: 0 10px;
			text-align: center;
			border: 1px solid $border;
			text-align: center;
			vertical-align: middle;
			&:first-child {
				@include extrabold;
				font-size: 21px; } } } }

.option {
	margin-bottom: 20px;
	user-select: none;
	&__title {
		margin-bottom: 10px;
		@include bold;
		font-size: 18px;
		text-transform: uppercase; }
	&__selected {
		margin-left: 5px;
		@include light;
		text-transform: none; }
	&__list {
		margin: 0 -10px -10px 0;
		font-size: 0; }
	&__item {
		display: inline-block;
		position: relative;
		margin: 0 10px 10px 0;
		vertical-align: top;
		cursor: pointer; }
	&__input {
		display: none; }
	&__input:checked + &__var {
		&:before {
			display: block; } }
	&__var {
		display: block;
		position: relative;
		&:before {
			content: "";
			display: none;
			position: absolute;
			top: -1px;
			left: -1px;
			right: -1px;
			bottom: -1px;
			border: 3px solid $blue;
			border-radius: 50%;
			z-index: 2; } }
	&__pic {
		width: 54px;
		height: 54px;
		border-radius: 50%; }
	&__size,
	&__color {
		display: block;
		width: 54px;
		height: 54px;
		border-radius: 50%; }
	&__size {
		border: 1px solid $border;
		background: linear-gradient(#f0f0f1, #e3e4e5);
		text-align: center;
		line-height: 52px;
		font-size: 22px; }
	&_sm &__title {
		margin-bottom: 5px; }
	&_sm &__pic {
		width: 42px;
		height: 42px; }
	&_sm &__size {
		width: 42px;
		height: 42px;
		line-height: 40px;
		font-size: 18px; } }

.select {
	position: relative;
	&__head {
		position: relative;
		height: 42px;
		padding: 0 34px 0 12px;
		border: 1px solid $border;
		background: linear-gradient(#fff, #e1e2e3);
		border-radius: 2px;
		font-size: 18px;
		white-space: nowrap;
		text-overflow: ellipsis;
		line-height: 40px;
		overflow: hidden;
		&:before,
		&:after {
			content: "";
			position: absolute; }
		&:before {
			top: 0;
			right: 0;
			width: 26px;
			height: 40px;
			border-left: 1px solid $border;
			border-left: 0 2px 2px 0;
			background: linear-gradient(to left, #fafafa, #e2e3e4); }
		&:after {
			top: 12px;
			right: 8px;
			@include arr(9,9,#fff,br);
			box-shadow: 1px 1px 0 $border;
			transform: rotate(45deg); } }
	&__el {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		border: none;
		cursor: pointer; } }

.form {
	margin-bottom: 40px;
	&__title {
		margin-bottom: 8px;
		@include bold;
		letter-spacing: 3px;
		@include rm(1023) {
			margin-bottom: 30px; } }
	&__head {
		@include clr; }
	&__paysys {
		float: right;
		@include rm(1023) {
			display: none; } }
	&__fieldset {
		margin-bottom: 25px; }
	&__fieldset_row {
		@include rm(1023) {
			margin: 0 -10px 40px;
			@include clr; } }
	&__field,
	&__option {
		@include rm(1023) {
			float: left;
			width: 50%;
			padding: 0 10px; } }
	&__option_sm {
		@include rm(1023) {
			width: 30%; } }
	&__option_md {
		@include rm(1023) {
			width: 35%; } }
	&__field_wide {
		@include rm(1023) {
			width: 100%; } }
	&__field_lg {
		@include rm(1023) {
			width: 65%; } }
	&__field_sm {
		@include rm(1023) {
			width: 35%; } }
	&__btn {
		width: 100%; }
	&__or {
		padding: 10px 0;
		font-size: 20px;
		text-transform: uppercase;
		text-align: center;
		@include rm(1023) {
			display: none; } }
	&__row {
		@include rm(1023) {
			margin: 0 -10px;
			@include clr; } }
	&__row_invert &__col {
		@include rm(1023) {
			float: right; } }
	&__col {
		@include rm(1023) {
			float: left;
			width: 50%;
			padding: 0 10px; } }
	&__close {
		display: none;
		position: absolute;
		top: 33px;
		right: -5px;
		width: 30px;
		background: #FFF;
		border-radius: 50%;
		@include extrabold;
		font-size: 22px;
		line-height: 30px;
		@include rm(719) {
			display: block; } }
	&_change {
		display: none;
		position: relative;
		margin: 0 0 20px;
		&.is-active {
			display: block; }
		@include r(718) {
			padding: 10px;
			border: 1px solid $border;
			border-radius: 5px; }
		@include rm(719) {
			display: block;
			margin: 0;
			padding-right: 30px;
			&:after {
				content: "";
				position: absolute;
				top: 30px;
				left: -50px;
				@include arr(20,40,#F1F2F2,l); } } }
	&_change &__option {
		&:last-child {
			margin: 0; } }
	&_change &__fieldset {
		margin: 0;
		@include rm(1023) {
			margin-left: -10px; } } }


.field {
	margin-bottom: 5px;
	&__label {
		margin-bottom: 5px;
		font-size: 18px;
		@include rm(719) {
			@include extrabold;
			font-size: 20px; } }
	&__label,
	&__wrap {
		&_hidden {
			display: none; } }
	&_style {
		margin-bottom: 20px; }
	&_style &__label {
		@include extrabold;
		font-size: 18px;
		text-transform: uppercase; } }

.input {
	width: 100%;
	height: 42px;
	padding: 0 12px;
	border: 1px solid $border;
	border-radius: 2px;
	@include light;
	font-size: 18px; }

.btn {
	height: 56px;
	padding: 0 20px;
	background: $pink;
	border-radius: 4px;
	@include light;
	font-size: 22px;
	color: #fff;
	letter-spacing: 1px;
	&:hover {
		background: $pink-light; }
	&:disabled {
		background: #BCBDC0;
		cursor: not-allowed; }
	&__pic,
	&__title {
		display: inline-block;
		vertical-align: middle; }
	&__pic {
		margin-left: 10px;
		width: 105px; }
	&_white {
		background: #FFF;
		border: 1px solid $border;
		color: $black;
		&:hover {
			background: $gray-light; } } }

.paysys {
	font-size: 0;
	&__item {
		display: inline-block;
		margin-left: 10px;
		vertical-align: top;
		&:first-child {
			margin: 0; } }
	&__logo {
		width: 33px; } }

.card {
	&__wrap {
		@include clr;
		@include r(1022) {
			height: 42px;
			padding: 0 12px;
			border: 1px solid $border;
			background: #FFF;
			border-radius: 2px; } }
	&__wrap.is-active &__number_full {
		@include r(1022) {
			display: none; } }
	&__wrap.is-active &__fieldset {
		@include r(1022) {
			display: block; } }
	&__preview {
		position: relative;
		float: left;
		width: 33px;
		height: 22px;
		margin: 10px 12px 0 0;
		border-radius: 2px;
		font-size: 0;
		perspective: 1000;
		transform-style: preserve-3d;
		@include rm(1023) {
			display: none; }
		&.is-active &-front {
			transform: rotateY(-180deg); }
		&.is-active &-back {
			transform: rotateY(0deg); }
		&-front,
		&-back {
			position: absolute;
			top: 0;
			left: 0;
			width: 33px;
			height: 22px;
			background: $border;
			border-radius: 2px;
			backface-visibility: hidden;
			transition: 0.6s;
			transform-style: preserve-3d; }
		&-front {
			transform: rotateY(0deg);
			z-index: 2; }
		&-back {
			transform: rotateY(180deg); }
		&-back &-pic {
			display: block; }
		&-pic {
			display: none;
			width: 33px;
			&.is-active {
				display: block; } } }
	&__logo {
		width: 33px; }
	&__number {
		overflow: hidden;
		&_full {
			@include rm(1023) {
				float: left;
				width: 50%; } }
		&_short {
			float: left;
